.feedback-modal .nextgen-modal__title {
  font-size: 1.75rem;
  margin-bottom: 1em;
  margin-top: 3px;
}
.feedback-modal__ratings {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin: 2em 3em 1em;
  padding: 0;
}
.feedback-modal__rating {
  display: inline-block;
  height: 64px;
  margin: 0 5px;
  width: 64px;
}
.feedback-modal__rating__button {
  background-color: #fff;
  border: 2px solid #1e1e1e;
  border-radius: 60px;
  box-shadow: 0 0 0 #000 !important;
  height: 60px;
  position: relative;
  transition: all 0.3s;
  width: 60px;
}
.feedback-modal__rating__button::before, .feedback-modal__rating__button::after {
  background-color: #1e1e1e;
  border-radius: 5px;
  content: "";
  display: block;
  height: 5px;
  position: absolute;
  top: 20px;
  transition: all 0.3s;
  width: 5px;
}
.feedback-modal__rating__button::before {
  left: 18px;
}
.feedback-modal__rating__button::after {
  right: 15px;
}
.feedback-modal__rating__button:hover, .feedback-modal__rating__button:active, .feedback-modal__rating__button.is-selected {
  border-color: var(--wp-admin-theme-color) !important;
  cursor: pointer;
}
.feedback-modal__rating__button:hover::before, .feedback-modal__rating__button:hover::after, .feedback-modal__rating__button:active::before, .feedback-modal__rating__button:active::after, .feedback-modal__rating__button.is-selected::before, .feedback-modal__rating__button.is-selected::after {
  background-color: var(--wp-admin-theme-color) !important;
}
.feedback-modal__rating__button:hover .feedback-modal__rating__smile, .feedback-modal__rating__button:active .feedback-modal__rating__smile, .feedback-modal__rating__button.is-selected .feedback-modal__rating__smile {
  border-color: var(--wp-admin-theme-color) !important;
}
.feedback-modal__rating__button:focus, .feedback-modal__rating__button:active {
  box-shadow: 0 0 0 #000;
  outline: none;
}
.feedback-modal__rating__button.is-selected {
  background-color: var(--everyday-blue-100);
}
.feedback-modal__rating__smile {
  border: 2px solid #1e1e1e;
  height: 15px;
  left: 50%;
  position: absolute;
  transform: translateX(-15px);
  transition: all 0.3s;
  width: 30px;
}
.feedback-modal__rating__smile:hover {
  cursor: pointer;
}
.feedback-modal__rating__smile--1 {
  border-bottom: 0;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  top: 32px;
}
.feedback-modal__rating__smile--2 {
  border-bottom-left-radius: 20px;
  border-right: 0;
  border-top: 0;
  height: 20px;
  top: 22px;
  transform: rotate(-225deg) translateX(13px);
  width: 20px;
}
.feedback-modal__rating__smile--3 {
  background-color: #1e1e1e;
  border-bottom: 0;
  height: 2px;
  top: 38px;
}
.feedback-modal__rating__smile--4 {
  border-bottom-left-radius: 20px;
  border-right: 0;
  border-top: 0;
  height: 20px;
  top: 19px;
  transform: rotate(-45deg) translateX(-13px);
  width: 20px;
}
.feedback-modal__rating__smile--5 {
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top: 0;
  top: 32px;
}
.feedback-modal__rating__value {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
.feedback-modal__comment {
  position: relative;
}
.feedback-modal__comment::before {
  border-bottom: 11px solid var(--everyday-blue-100);
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  content: "";
  display: block;
  height: 14px;
  left: 50%;
  position: absolute;
  top: -9px;
  transform: translateX(-50%);
  transition: left 0.2s;
  width: 20px;
}
.feedback-modal__comment--1::before {
  left: calc(50% - 148px);
}
.feedback-modal__comment--2::before {
  left: calc(50% - 74px);
}
.feedback-modal__comment--4::before {
  left: calc(50% + 74px);
}
.feedback-modal__comment--5::before {
  left: calc(50% + 148px);
}
.feedback-modal__comment__textarea {
  background-color: var(--everyday-blue-100);
  border: 0;
  border-radius: 0;
  box-shadow: none;
  left: -25px;
  margin-bottom: 1.75em;
  min-height: 150px;
  padding: 20px;
  position: relative;
  width: calc(100% + 50px);
}
.feedback-modal__comment__textarea:focus {
  border: 0;
  box-shadow: 0 0 0 #000;
  outline: 0;
}
.feedback-modal__success svg {
  width: 65px;
}
.feedback-modal__success .feedback-modal__success__msg {
  line-height: 1.2;
  margin-bottom: 1em;
  margin-top: 0.75em;
  max-width: 300px;
}
.feedback-modal .components-modal__header {
  border-bottom: 0;
  height: 50px;
  margin: 0 -60px;
}
.feedback-modal .components-modal__content {
  overflow-x: hidden;
  padding: 0 48px 30px;
  text-align: center;
}

.feedback-modal-button {
  display: none;
  height: auto;
  order: -1;
  padding-bottom: 0;
  padding-top: 0;
}
@media (min-width: 782px) {
  .feedback-modal-button {
    display: inherit;
  }
}
.feedback-modal-button svg {
  margin-right: 5px;
  position: relative;
  top: 1px;
}
.feedback-modal-button span {
  overflow: hidden;
}